/* eslint-disable */
import { supabase } from './supabase';

const STORAGE_BUCKET = 'schedule-attachments';
const MAX_FILE_SIZE = 10 * 1024 * 1024; // 10MB

/**
 *   
 */
export interface UploadResult {
  success: boolean;
  url?: string;
  path?: string;
  error?: string;
}

/**
 *  Supabase Storage .
 * @param file  
 * @param userId  ID
 * @param scheduleId  ID
 * @returns  
 */
export async function uploadFile(
  file: File,
  userId: string,
  scheduleId: number
): Promise<UploadResult> {
  try {
    //   
    if (file.size > MAX_FILE_SIZE) {
      return {
        success: false,
        error: '  10MB   .',
      };
    }

    //   
    const fileExt = file.name.split('.').pop();
    const fileName = `${Date.now()}-${Math.random().toString(36).substring(7)}.${fileExt}`;
    const filePath = `${userId}/${scheduleId}/${fileName}`;

    // Supabase Storage 
    if (!supabase) {
      return {
        success: false,
        error: '  ',
      };
    }

    const { error } = await supabase.storage
      .from(STORAGE_BUCKET)
      .upload(filePath, file, {
        cacheControl: '3600',
        upsert: false,
      });

    if (error) {
      console.error(' File upload error:', error);
      return {
        success: false,
        error: error.message,
      };
    }

    // Public URL 
    const { data: { publicUrl } } = supabase.storage
      .from(STORAGE_BUCKET)
      .getPublicUrl(filePath);

    return {
      success: true,
      url: publicUrl,
      path: filePath,
    };
  } catch (error: any) {
    console.error(' Unexpected file upload error:', error);
    return {
      success: false,
      error: error.message || '    .',
    };
  }
}

/**
 * Supabase Storage  .
 * @param filePath  
 * @returns   
 */
export async function deleteFile(filePath: string): Promise<boolean> {
  try {
    if (!supabase) {
      console.error(' Supabase not initialized');
      return false;
    }

    const { error } = await supabase.storage
      .from(STORAGE_BUCKET)
      .remove([filePath]);

    if (error) {
      console.error(' File delete error:', error);
      return false;
    }

    return true;
  } catch (error) {
    console.error(' Unexpected file delete error:', error);
    return false;
  }
}

/**
 *      .
 * @param fileName 
 * @returns  
 */
export function getOriginalFileName(fileName: string): string {
  // timestamp random string 
  const parts = fileName.split('-');
  if (parts.length >= 2) {
    return parts.slice(1).join('-');
  }
  return fileName;
}

/**
 *       .
 * @param bytes  
 * @returns  
 */
export function formatFileSize(bytes: number): string {
  if (bytes === 0) return '0 Bytes';

  const k = 1024;
  const sizes = ['Bytes', 'KB', 'MB', 'GB'];
  const i = Math.floor(Math.log(bytes) / Math.log(k));

  return Math.round(bytes / Math.pow(k, i) * 100) / 100 + ' ' + sizes[i];
}

/**
 *     .
 * @param fileName 
 * @returns  
 */
export function getFileIcon(fileName: string): string {
  const ext = fileName.split('.').pop()?.toLowerCase();

  const iconMap: Record<string, string> = {
    // 
    jpg: '',
    jpeg: '',
    png: '',
    gif: '',
    // 
    pdf: '',
    doc: '',
    docx: '',
    xls: '',
    xlsx: '',
    ppt: '',
    pptx: '',
    // 
    zip: '',
    rar: '',
    // 
    txt: '',
  };

  return iconMap[ext || ''] || '';
}
